
.avatar-large img, .avatar-large svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display:block
}

.hidden {
  display:none !important
}

.message-embed {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  padding: 16px;
  border-radius: 4px;
  background: #36393f;
  color: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, .3);
  border-left: 4px solid #73C394;
  display: flex;
  align-items: flex-start;
  margin-top:8px
}

.embed-title {
  font-weight: 500;
  word-break:break-word
}

.embed-description {
  margin-top: 16px;
  color: #ddd;
  font-size: 14px;
  word-break:break-word
}

a[href] {
  color:#1eb1f1
}

.embed-footer a[href] {
  color:#fff;
  text-decoration: none;
}
.embed-fields .embed-field {
  margin-top: 16px;
  font-size:14px
}

.embed-fields .embed-field .embed-field-name {
  font-weight:500
}

.embed-fields .embed-field .embed-field-value {
  margin-top: 4px;
  word-break:break-word
}

.embed-fields .embed-field.inline {
  display: inline-block;
  margin-right:16px
}

.embed-thumbnail {
  margin-left: auto;
  padding-left:16px
}

.embed-author {
  display: flex;
  align-items: center;
  font-size:14px
}

.embed-author a[href] {
  color:#fff
}

.embed-author .author-image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 8px;
  object-fit:cover
}

.embed-footer {
  display: flex;
  align-items: center;
  margin-top: 8px;
  font-size:10px
}

.embed-footer .footer-icon {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 8px;
  flex-shrink: 0;
  object-fit:cover
}

.embed-footer .divider {
  margin:0 8px
}

.avatar-large {
  width: 64px;
  height: 64px;
  overflow: hidden;
  border-radius: 4px;
  flex-shrink:0
}